button {
	padding: 3px;
}

.section-header {
	/* margin-bottom: 60px; */
}

.grid-container {
	zoom: 1;
}

.grid-container:after {
	content: '';
	display: table;
	clear: both;
}

.grid-container {
	max-width: 1280px;
	margin: 0 auto;
	padding-top: 10px;
	display: flex;
	flex-direction: row;
}

.look-img-trigger {
	background: #fff;
	z-index: 1;
	margin: 0;
	cursor: pointer;
}

.look-img-trigger figure,
.look-img-trigger img {
	height: 100%;
	object-fit: cover;
	object-position: top;
}

div.look {
	margin: 0 auto;
	width: 100%;
	display: flex;
	flex-direction: column;
	/* justify-content: space-evenly; */
	/* flex-wrap: wrap; */
	/* object-fit: cover; */
	box-shadow: 0px 0px 2px 0px rgb(104 104 104 / 55%);
}

.desc-container {
	width: 100%;
}

.look-description {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	align-self: center;
	padding-top: 20px;
	color: #000;
}

.look-description h4 {
	margin: 0 0 20px 0;
	padding: 0;
}

.look-description ul {
	display: none;
	align-self: center;
	margin: 0 0 20px 0;
	padding: 0;
}

.look-description li {
	padding: 0 0 10px 0;
	line-height: 1.25rem;
	font-weight: 400;
}

button.resetHidden {
	display: none;
}

button.resetVisible {
	text-align: left;
	display: block;
	cursor: pointer;
	color: #666;
	text-decoration: underline;
}

.outfit-img-container {
	width: 100%;
	z-index: 5;
}

.outfit-image {
	object-fit: cover;
	max-height: 1000px;
}

.tooltip {
	font-size: 1.25rem;
	position: absolute;
	padding: 20px;
	width: 90%;
	top: 10px;
	z-index: 20;
	background: rgba(95, 133, 147, 0.2);
	visibility: visible;
	opacity: 1;
	transition: visibility 0s linear 300ms, opacity 300ms;
}

.tooltip.hide {
	visibility: hidden;
	opacity: 0;
}

.item_list {
	cursor: pointer;
	display: flex;
	flex-direction: row;
	max-width: 100%;
	background: #fff;
	justify-content: space-evenly;
	z-index: 10;
	transform: translateY(0);
	transition: all 1s linear;
}

.item_list.open {
	transform: translateY(-600px);
	-webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
	-moz-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
	-o-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
	box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
}

.item {
	align-self: center;
}

.item p {
	/* font-weight: bold; */
	text-align: center;
	font-size: 0.8rem;
}

div.tall_group {
	display: flex;
	flex-direction: column;
	justify-content: center;
	/* max-width: 36%; */
}

div.short_group {
	display: flex;
	flex-direction: column;
	justify-content: center;
	/* max-width: 26%; */
}

.tall_group img {
	object-fit: cover;
	max-height: 400px;
	width: auto;
}

.tall_group div:first-child img {
	cursor: pointer;
	object-fit: contain;
	max-height: 340px;
	width: auto;
}

.short_group img {
	object-fit: cover;
	max-height: 280px;
	width: auto;
}

.open .tall_group img {
	object-fit: cover;
	max-height: 300px;
	width: auto;
	transition: all 1s linear;
}

.open .tall_group div:first-child img {
	max-height: 260px;
	width: auto;
}

.open .short_group img {
	object-fit: cover;
	max-height: 180px;
	width: auto;
	transition: all 1s linear;
}

