.item-opts-container {
	max-height: 0px;
	color: #000;
	background: #fff;
	width: 100%;
	overflow-y: scroll;
	z-index: 1;
	transform: translateY(0);
	transition: all 1s linear;
}

.item-opts-container.visible {
	display: flex;
	flex-direction: column;
	max-height: 600px;
	justify-self: flex-start;
	transform: translateY(-600px);
	margin-bottom: -600px;
}

.panel-header.sticky {
	position: -webkit-sticky;
	position: sticky;
	width: 100%;
	display: flex;
	flex-direction: row;
	/* justify-content: flex-start; */
	align-self: flex-start;
	top: 0;
	height: 100px;
	background: #f7f7f7;
	z-index: 0;
	border-bottom: 1px solid #eee;
	margin: 0 0 12px 0;
	padding: 24px 12px 0 24px;
}

div.item-opt {
	display: flex;
	flex-direction: row;
	max-width: 100%;
	margin-bottom: 20px;
	align-items: center;
}

.item-opt p {
	line-height: 1.4em;
	margin: 0 0 0 20px;
	padding: 0;
}

.item-opt img {
	max-width: 30%;
	height: auto;
	margin-left: 24px;
	cursor: pointer;
}

.item-opt img.chosen {
	border: 1px solid #9a9a9a;
}

