		display: block;
	}
	div.look {
		margin: 0 auto;
		max-width: 1280px;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		justify-content: space-evenly;
		margin-top: 20px;
		max-height: 500px;
	}
	.desc-container {
		max-width: 20%;
		padding: 20px;
	}
	.look-description {
		display: flex;
		flex-direction: column;
		justify-content: center;
		min-height: 100%;
	}
	.look-description h2,
	.look-description ul {
		align-self: flex-start;
	}
	.look-description ul {
		display: block;
	}
	.look-description li {
		padding: 0 0 10px 0;
		line-height: 1.5rem;
	}

	.outfit-img-container {
		max-width: 30%;
	}
	.outfit-image {
		/* object-fit: cover; */
		max-height: 100%;
		width: auto;
	}
	.tooltip {
		font-size: 0.875rem;
		position: absolute;
		padding: 10px;
		width: 100%;
		top: 10px;
		z-index: 20;
		left: 40px;
		background: rgba(95, 133, 147, 0.2);
		visibility: visible;
		opacity: 1;
		transition: visibility 0s linear 300ms, opacity 300ms;
	}
	.tooltip.hide {
		visibility: hidden;
		opacity: 0;
	}
	div.item_list {
		position: relative;
		display: flex;
		flex-direction: row;
		max-width: 40%;
		/* background: rgb(155, 165, 238) !important; */
		justify-content: space-evenly;
		transform: translateX(100px);
		transition: all 1s linear;
	}
	div.item_list.open {
		transform: translateX(0);
		-webkit-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
		-moz-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
		-o-box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
		box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
	}
	.item p {
		margin: 0;
		padding: 0;
	}
	div.tall_group {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	.tall_group div:first-child img {
		cursor: pointer;
		object-fit: contain;
		max-height: 160px;
		width: auto;
	}
	.open .tall_group div:first-child img {
		max-height: 160px;
	}
