	.item-opts-container {
		color: #000;
		background: #fff;
		max-height: 500px;
		max-width: 25%;
		opacity: 0;
		display: flex;
		flex-direction: column;
		justify-self: flex-start;
		overflow-y: auto;
		z-index: 1;
		transform: translateX(-300px);
		transition: all 0.75s linear;
